<template>
  <div class="elContent">
    <el-container style="min-height: 100%">
      <el-header style="border-bottom: 1px solid #e6e6e6;font-weight: bold">
        <i class="el-icon-s-ticket logo"></i>
        XXX
      </el-header>
      <el-container>
        <el-aside width="200px">
          <el-row class="tac" style="height: 100%">
            <el-col :span="24" style="height: 100%">
              <el-menu
                :default-active="defaultActive"
                class="el-menu-vertical-demo"
                @open="handleOpen"
                @close="handleClose"
                @select="selectMenuItem"
                style="height: 100%"
              >
                <el-submenu index="1">
                  <template slot="title">
                    <i class="el-icon-location"></i>
                    <span></span>
                  </template>
                  <el-menu-item-group>
                    <template slot="title"></template>
                    <el-menu-item index="1-1"></el-menu-item>
                    <el-menu-item index="1-2"></el-menu-item>
                    <el-menu-item index="1-3"></el-menu-item>
                  </el-menu-item-group>
                  <el-menu-item-group title="">
                    <el-menu-item index="1-4"></el-menu-item>
                  </el-menu-item-group>
                  <el-submenu index="1-5">
                    <template slot="title"></template>
                    <el-menu-item index="1-5-1"></el-menu-item>
                  </el-submenu>
                </el-submenu>
                <el-menu-item index="2">
                  <i class="el-icon-menu"></i>
                  <span slot="title"></span>
                </el-menu-item>
                <el-menu-item index="3" disabled>
                  <i class="el-icon-document"></i>
                  <span slot="title"></span>
                </el-menu-item>
                <el-menu-item index="4">
                  <i class="el-icon-setting"></i>
                  <span slot="title"></span>
                </el-menu-item>
              </el-menu>
            </el-col>
          </el-row>
        </el-aside>
        <el-main>
          <el-tabs v-model="activeName" @tab-click="handleClick" @tab-remove="removeTab">
            <el-tab-pane
              v-for="(item) in editableTabs"
              :key="item.navIndex"
              :label="item.title"
              :name="item.name"
              closable
            >
              <router-view />
            </el-tab-pane>
          </el-tabs>
        </el-main>
      </el-container>
    </el-container>
  </div>
</template>
<script>
export default {
  data() {
    return {
      activeName: "1-1",
      navTabs: {
        "1-1": {
          name: "",
          pathName: "main"
        },
        "1-2": {
          name: "",
          pathName: "usermanage"
        },
        "1-3": {
          name: "",
          pathName: "usersetup"
        },
        "1-4": {
          name: "",
          pathName: "ticketinfo"
        },
        "1-5-1": {
          name: "",
          pathName: "query"
        },
        "2": {
          name: "",
          pathName: "workstation"
        },
        "4": {
          name: "",
          pathName: "messagecenter"
        }
      },
      editableTabs: [
        {
          title: "",
          name: "1-1",
          navIndex: "1-1"
        },
        {
          title: "",
          name: "1-2",
          navIndex: "1-2"
        },
        {
          title: "",
          name: "1-3",
          navIndex: "1-3"
        }
      ],
      editableTabsValue: "2",
      tabIndex: 2
    };
  },
  computed: {
    defaultActive() {
      return this.activeName;
    }
  },
  methods: {
    handleClick({ name }) {
      this.$router.push({ name: this.navTabs[name].pathName });
    },
    handleOpen() {},
    handleClose() {},
    selectMenuItem(index, indexPath) {
      console.log(index);
      this.addTab(index);
      this.$router.push({ name: this.navTabs[index].pathName });
    },
    addTab(navIndex) {
      this.activeName = navIndex;
      let hasAdded = false;
      this.editableTabs.forEach(function(item, index) {
        if (item.navIndex == navIndex) {
          hasAdded = true;
          return;
        }
      });
      console.log(hasAdded, this.editableTabs);
      if (!hasAdded) {
        this.editableTabs.push({
          title: this.navTabs[navIndex + ""].name,
          navIndex,
          name: navIndex
        });

        console.log(this.editableTabs);
      }
    },
    removeTab(targetName) {
      console.log(targetName);
      let tabs = this.editableTabs;
      if (tabs.length === 1) {
        return;
      }
      if (this.activeName === targetName) {
        tabs.forEach((tab, index) => {
          if (tab.name === targetName) {
            let nextTab = tabs[index + 1] || tabs[index - 1];
            if (nextTab) {
              this.activeName = nextTab.name;
              this.$router.push({
                name: this.navTabs[this.activeName].pathName
              });
            }
          }
        });
      }

      this.editableTabs = tabs.filter(tab => tab.name !== targetName);
      this.tabIndex -= 1;
    }
  }
};
</script>
<style lang="scss" scoped>
@import "../globalset";
.elContent {
  width: 84%;
  height: 100%;
  overflow: hidden;
  box-sizing: border-box;
  padding: 0 !important;
}
.logo {
  margin-right: 8px;
  padding: 8px;
  background: $defaultThemeColor;
  border-radius: 50%;
  margin-top: 14px;
  color: #FFF;
}
</style>