<template>
  <div>
    <p></p>
    <p>$emit  this.$on('customEvent', function(arg){})</p>
    <p>$on  this.$emit('customEvent',123)</p>
    <button v-on:click="eventHandler" class="goto1">push</button>
    <p></p>
    <p>$off  this.$off('customEvent')</p>
    <button v-on:click="detachEvent" class="goto1"></button>
    <p> </p>
  </div>
</template>
<script>
export default {
  name: "customevent",
  mounted() {
    this.$on("customEvent", function(arg) {
      console.log(arg);
    });
  },
  methods: {
    eventHandler() {
      this.$emit("customEvent", 123);
    },
    detachEvent() {
      this.$off("customEvent");
    }
  }
};
</script>
<style lang="scss" scoped>
@import "../globalset";
.goto1 {
  display: block;
  padding: 10px 15px;
  height: 40px;
  outline: none;
  border: none;
  background: $defaultThemeColor;
  cursor: pointer;
  font-size: 14px;
  margin: 28px 0;
  border-radius: 8px;
  color: #fff
}
</style>