<template>
  <div>
    <h4> vuex </h4>
    <section class="display">
      <p class="tip">
        $store.state.userData.name
        <span class="empha">{{$store.state.userData.name}}</span>
      </p>
      <p class="tip">
        $store.state.userData.permission
        <span class="empha">{{$store.state.userData.permission}}</span>
      </p>
      <p class="tip">
        $store.state.userData.number
        <span class="empha">{{$store.state.userData.number}}</span>
      </p>
    </section>
    <h4> getters </h4>
    <section class="display">
      <p class="tip">
        $store.getters.username
        <span class="empha">{{$store.getters.username}}</span>
      </p>
    </section>
    <h4> mutations </h4>
    <section class="display">
      <div class="tip">
        <p>
          $store.getters.username
          <span class="empha">{{$store.getters.username}}</span>
        </p>
        <p>
          this.$store.commit('setUserName','wallen')
          <button
            @click="handleSetUserNameClick"
            class="btn"
          >push to update username</button>
        </p>
      </div>
    </section>
    <section class="display" style="margin-top: 20px">
      <div class="tip">
        <p>
          $store.getters.userNumber
          <span class="empha">{{$store.getters.userNumber}}</span>
        </p>
        <p>
          this.$store.commit('setUserNumber', 10)
          <button
            @click="handleSetUserNumberClick"
            class="btn"
          >push to increment 10</button>
        </p>
      </div>
    </section>
    <h4> actions </h4>
    <section class="display">
      <div class="tip">
        <p>
          $store.getters.username
          <span class="empha">{{$store.getters.username}}</span>
        </p>
        <p>
          this.$store.dispatch('setUserNameAsync','wallen')
          <button
            @click="handleSetUserNameAsyncClick"
            class="btn"
          >push to update username</button>
        </p>
      </div>
    </section>
    <section class="display" style="margin-top: 20px">
      <div class="tip">
        <p>
          $store.getters.userNumber
          <span class="empha">{{$store.getters.userNumber}}</span>
        </p>
        <p>
          this.$store.dispatch('setUserNumberAsync', 10)
          <button
            @click="handleSetUserNumberAsyncClick"
            class="btn"
          >push to increment 10</button>
        </p>
      </div>
    </section>
  </div>
</template>
<script>
export default {
  name: "datadisplay",
  data() {
    return {};
  },
  methods: {
    handleSetUserNameClick() {
      this.$store.commit("setUserName", "wallen");
    },
    handleSetUserNumberClick() {
      this.$store.commit("setUserNumber", 10);
    },
    handleSetUserNameAsyncClick() {
      this.$store.dispatch("setUserNameAsync", "wallen");
    },
    handleSetUserNumberAsyncClick() {
      this.$store.dispatch("setUserNumberAsync", 10);
    }
  }
};
</script>
<style lang="scss" scoped>
@import "../globalset";
.tip {
  color: #aaa;
}
.empha {
  color: #000;
  font-weight: bold;
  margin-left: 2em;
}
.display {
  padding: 10px 24px;
  background: #f7f7f7;
  border-radius: 5px;
}
.btn {
  display: inline-block;
  padding: 10px 15px;
  height: 40px;
  outline: none;
  border: none;
  background: $defaultThemeColor;
  cursor: pointer;
  font-size: 14px;
  margin: 0 30px;
  border-radius: 8px;
  color: #fff
}
</style>