import Vue from "vue"
import Router from "vue-router"
import Home from "../views/Home.vue"
import Posts from "../views/Posts.vue"
import Profile from "../views/Profile.vue"
import Setup from "../views/Setup.vue"
import Tools from "../views/Tools.vue"
import Postcertain from "../views/Postcertain.vue"
import Hi from "../views/Hi.vue"
import H1 from "../components/H1.vue"
import H2 from "../components/H2.vue"
import Multi from "../views/Multi.vue"
import Pass from "../views/Pass.vue"
import Customevent from "../views/Customevent.vue"
import Dashboard from "../views/Dashboard.vue"
import Datadisplay from "../views/Datadisplay.vue"
import Elem from "../views/Elem.vue"
import Main from "../views/user/Main.vue"
import Usermanage from "../views/user/Usermanage.vue"
import Usersetup from "../views/user/Usersetup.vue"
import Ticketinfo from "../views/ticket/Ticketinfo.vue"
import Query from "../views/query/Query.vue"
import Workstation from "../views/workstation/Workstation.vue"
import Messagecenter from "../views/center/Messagecenter.vue"

Vue.use(Router)

const router = new Router({
  mode: "history",
  base: process.env.BASE_URL,
  routes: [
    {
      path: "/",
      redirect: "/posts",
      component: Posts
    },
  
    {
      path: "/profile",
      name: "profile",
      component: Profile,
      meta: {title: ""}
    },

    {
      path: "/posts",
      name: "posts",
      component: Posts,
      meta: {title: ""}
    },
    {
      path: "/setup",
      name: "setup",
      component: Setup,
      meta: {title: ""}
    },
    {
      path: "/tools",
      name: "tools",
      component: Tools,
      meta: {title: ""}
    },
    {
      path: "/posts/:userId/:postId",
      name: "postcertain",
      component: Postcertain,
      meta: {title: ""}
    },
    {  // 
      path: "/hi",      
      name: "hi",
      component: Hi,   
      children: [
        {path: "/hi/h1", name:"h1", component: H1},
        {path: "/hi/h2", name:"h2", component: H2}
      ]
    },
    {
      path: "/multi",
      name: "multi",
      components: {   //
        default: Multi,
        left: H1,
        right: H2
      }
    },
    {
      path: "/pass",
      name: "pass",
      component: Pass,
      meta: {title: ""}
    },
    {
      path: "/customevent",
      name: "customevent",
      component: Customevent,
      meta: {title: "$on $emit"}
    },
    {
      path: "/dashboard",
      name: "dashboard",
      component: Dashboard,
      meta: {title: "Echarts"}
    },
    {
      path: "/datadisplay",
      name: "datadisplay",
      component: Datadisplay,
      meta: {title: "Vuex"}
    },
    {
      path: "/elem",
      name: "elem",
      component: Elem,
      meta: {title: " Element-UI"},
      children: [
        {path: "/user/main", name:"main", component: Main},
        {path: "/user/manage", name:"usermanage", component: Usermanage},
        {path: "/user/setup", name:"usersetup", component: Usersetup},
        {path: "/query/query", name:"query", component: Query},
        {path: "/ticket/ticketinfo", name:"ticketinfo", component: Ticketinfo},
        {path: "/workstation/workstation", name:"workstation", component: Workstation},

        {path: "/center/messagecenter", name:"messagecenter", component: Messagecenter}
      ]
    },
    {
      path: "*",
      component: Home


    }



  ]


})

router.beforeEach((to, from, next)=>{
  if(to.meta.title){
    document.title = to.meta.title
  }
  next()
})


export default router;