import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

const state = {
  userData: {
    name: 'gastby',
    permission: 'management',
    number: 16
  }
}

const getters = {
  username: state => state.userData.name,
  userNumber: state => state.userData.number
}

const mutations = {
  setUserName(state, newVal) {
    if (newVal) {
      state.userData.name = newVal
    } else { }
  },
  setUserNumber(state, newVal) {
    if (newVal) {
      state.userData.number += newVal
    } else { }
  }
}

let time1, time2

const actions = {
  setUserNameAsync: ({ commit }, payload) => {
    clearTimeout(time1)
    time1 = setTimeout(
      function () {
        commit('setUserName', payload)
      }
      , 1000)
  },
  setUserNumberAsync: ({ commit }, payload) => {
    clearTimeout(time2)
    time2 = setTimeout(
      function () {
        commit('setUserNumber', payload);
        console.log('111')
      }
      , 1000)
  }
}

export default new Vuex.Store({
  state,
  getters,
  mutations,
  actions
})